import React from "react";
// javascript plugin used to create scrollbars on windows
import PerfectScrollbar from "perfect-scrollbar";
import { Route, Switch } from "react-router-dom";

import DemoNavbar from "components/Navbars/DemoNavbar.jsx";
import Footer from "components/Footer/Footer.jsx";
// import Sidebar from "components/Sidebar/Sidebar.jsx";
import LoginForm from "components/LoginForm/Login.jsx";
import routes from "routes.js";

import withFirebaseAuth from "react-with-firebase-auth";
import firebase from "firebase";
import { firebaseConfig } from "variables/firebase";
import "firebase/auth";
import "firebase/database";
import { isThisTypeNode } from "typescript";

var ps;

const firebaseApp = firebase.initializeApp(firebaseConfig);

class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pedidos: [],
      backgroundColor: "black",
      activeColor: "info",
    };
    this.mainPanel = React.createRef();

    if (firebase.apps.length === 0) {
      this.app = firebase.initializeApp(firebaseConfig);
    }

    this.db = firebase.database();
    this.auth = firebase.auth();

    this.providers = {
      googleProvider: new firebase.auth.GoogleAuthProvider(),
    };
  }

  componentWillMount() {
    var ref = this.db.ref("pedidos");
    ref.on(
      "value",
      (snapshot) => {
        console.log("document", snapshot.val());
        const vals = snapshot.val();
        let pedidos = [];
        for (var key in vals) {
          pedidos.push({
            ...vals[key],
            id: key,
          });
        }
        this.setState({ pedidos });
      },
      function (errorObject) {
        console.log("The read failed: " + errorObject.code);
      }
    );
  }

  componentDidMount() {
    if (navigator.platform.indexOf("Win") > -1) {
      ps = new PerfectScrollbar(this.mainPanel.current);
      document.body.classList.toggle("perfect-scrollbar-on");
    }
  }
  componentWillUnmount() {
    if (navigator.platform.indexOf("Win") > -1) {
      ps.destroy();
      document.body.classList.toggle("perfect-scrollbar-on");
    }
  }
  componentDidUpdate(e) {
    if (e.history.action === "PUSH") {
      this.mainPanel.current.scrollTop = 0;
      document.scrollingElement.scrollTop = 0;
    }
  }
  handleActiveClick = (color) => {
    this.setState({ activeColor: color });
  };
  handleBgClick = (color) => {
    this.setState({ backgroundColor: color });
  };
  render() {
    return this.props.user ? (
      <div className='wrapper'>
        {/* <Sidebar
          {...this.props}
          routes={routes}
          bgColor={this.state.backgroundColor}
          activeColor={this.state.activeColor}
        /> */}
        <div
          className='main-panel'
          ref={this.mainPanel}
          style={{ width: "100vw" }}
        >
          <DemoNavbar {...this.props} />
          <Switch pedidos={this.state.pedidos}>
            {routes.map((prop, key) => {
              return (
                <Route
                  path={prop.layout + prop.path}
                  render={(props) => (
                    <prop.component pedidos={this.state.pedidos} />
                  )}
                  key={key}
                />
              );
            })}
          </Switch>
          <Footer fluid />
        </div>
      </div>
    ) : (
      <LoginForm {...this.props} patata={"hola"} />
    );
  }
}

const firebaseAppAuth = firebaseApp.auth();
const providers = {
  googleProvider: new firebase.auth.GoogleAuthProvider(),
};

// export default Dashboard;
export default withFirebaseAuth({
  providers,
  firebaseAppAuth,
})(Dashboard);
