import React from "react";
// react plugin used to create charts
import { Line, Pie } from "react-chartjs-2";
// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardTitle,
  Row,
  Col,
} from "reactstrap";
// core components
import {
  dashboard24HoursPerformanceChart,
  dashboardEmailStatisticsChart,
  dashboardNASDAQChart,
} from "variables/charts.jsx";

class Dashboard extends React.Component {
  darDataPedidosTemporal = (pedidos) => {};

  darMejoresProductos = (pedidos) => {
    let struct = { ...dashboardEmailStatisticsChart.data() };

    if (pedidos) {
      let productos = [];
      pedidos &&
        pedidos.length &&
        pedidos.forEach((pedido) => {
          pedido &&
            pedido.items &&
            pedido.items.length &&
            pedido.items.forEach((item) => {
              let prod = productos.find((p) => p["nombre"] === item["nombre"]);
              if (prod) {
                prod["cantidad"] = prod["cantidad"] + 1;
              } else {
                productos.push(item);
              }
            });
        });
      productos.sort(function (a, b) {
        return b.cantidad - a.cantidad;
      });
      productos = productos.slice(0, 4);

      let labels = [];
      let data = [];
      productos.forEach((p) => {
        labels.push(p.nombre);
        data.push(p.cantidad);
      });

      struct.labels = labels;
      struct.datasets[0].label = "productos";
      struct.datasets[0].data = data;
      console.log("netro", struct);
    }
    return struct;
  };

  render() {
    return (
      <>
        <div className='content'>
          <Row>
            <Col lg='3' md='6' sm='6'>
              <Card className='card-stats'>
                <CardBody>
                  <Row>
                    <Col md='4' xs='5'>
                      <div className='icon-big text-center icon-warning'>
                        <i className='nc-icon nc-basket text-warning' />
                      </div>
                    </Col>
                    <Col md='8' xs='7'>
                      <div className='numbers'>
                        <p className='card-category'>Pedidos</p>
                        <CardTitle tag='p'>
                          {this.props.pedidos
                            ? this.props.pedidos.length || 0
                            : 0}
                        </CardTitle>
                        <p />
                      </div>
                    </Col>
                  </Row>
                </CardBody>
                <CardFooter>
                  <hr />
                  <div className='stats'>
                    <i className='fas fa-sync-alt' /> Actualizar ahora
                  </div>
                </CardFooter>
              </Card>
            </Col>
            <Col lg='3' md='6' sm='6'>
              <Card className='card-stats'>
                <CardBody>
                  <Row>
                    <Col md='4' xs='5'>
                      <div className='icon-big text-center icon-warning'>
                        <i className='nc-icon nc-check-2 text-success' />
                      </div>
                    </Col>
                    <Col md='8' xs='7'>
                      <div className='numbers'>
                        <p className='card-category'>Correctos</p>
                        <CardTitle tag='p'>
                          {" "}
                          {this.props.pedidos
                            ? Math.round(this.props.pedidos.length / 5) * 4 || 0
                            : 0}
                        </CardTitle>
                        <p />
                      </div>
                    </Col>
                  </Row>
                </CardBody>
                <CardFooter>
                  <hr />
                  <div className='stats'>
                    <i className='fas fa-sync-alt' /> Actualizar ahora
                  </div>
                </CardFooter>
              </Card>
            </Col>
            <Col lg='3' md='6' sm='6'>
              <Card className='card-stats'>
                <CardBody>
                  <Row>
                    <Col md='4' xs='5'>
                      <div className='icon-big text-center icon-warning'>
                        <i className='nc-icon nc-simple-remove text-danger' />
                      </div>
                    </Col>
                    <Col md='8' xs='7'>
                      <div className='numbers'>
                        <p className='card-category'>Fallidos</p>
                        <CardTitle tag='p'>
                          {this.props.pedidos
                            ? Math.round(
                                (this.props.pedidos.length -
                                  Math.round(this.props.pedidos.length / 5) *
                                    4) /
                                  3
                              ) || 0
                            : 0}
                        </CardTitle>
                        <p />
                      </div>
                    </Col>
                  </Row>
                </CardBody>
                <CardFooter>
                  <hr />
                  <div className='stats'>
                    <i className='fas fa-sync-alt' /> Actualizar ahora
                  </div>
                </CardFooter>
              </Card>
            </Col>
            <Col lg='3' md='6' sm='6'>
              <Card className='card-stats'>
                <CardBody>
                  <Row>
                    <Col md='4' xs='5'>
                      <div className='icon-big text-center icon-warning'>
                        <i className='nc-icon nc-delivery-fast text-primary' />
                      </div>
                    </Col>
                    <Col md='8' xs='7'>
                      <div className='numbers'>
                        <p className='card-category'>En proceso</p>
                        <CardTitle tag='p'>
                          {this.props.pedidos && this.props.pedidos.length
                            ? Math.round(
                                (this.props.pedidos.length -
                                  Math.round(this.props.pedidos.length / 5) *
                                    4) /
                                  2
                              )
                            : 0}
                        </CardTitle>
                        <p />
                      </div>
                    </Col>
                  </Row>
                </CardBody>
                <CardFooter>
                  <hr />
                  <div className='stats'>
                    <i className='fas fa-sync-alt' /> Actualizar ahora
                  </div>
                </CardFooter>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col md='4'>
              <Card>
                <CardHeader>
                  <CardTitle tag='h5'>Productos top</CardTitle>
                  <p className='card-category'>Productos más pedidos</p>
                </CardHeader>
                <CardBody>
                  <Pie
                    data={this.darMejoresProductos(this.props.pedidos)}
                    options={dashboardEmailStatisticsChart.options}
                  />
                </CardBody>
                <CardFooter>
                  <div className='legend'>
                    <i className='fa fa-circle text-primary' />{" "}
                    {this.darMejoresProductos(this.props.pedidos).labels[0]}{" "}
                    <i className='fa fa-circle text-warning' />{" "}
                    {this.darMejoresProductos(this.props.pedidos).labels[1]}{" "}
                    <i className='fa fa-circle text-danger' />{" "}
                    {this.darMejoresProductos(this.props.pedidos).labels[2]}{" "}
                    <i className='fa fa-circle text-gray' />{" "}
                    {this.darMejoresProductos(this.props.pedidos).labels[3]}
                  </div>
                  <hr />
                  <div className='stats'>
                    <i className='fa fa-calendar' /> Number of emails sent
                  </div>
                </CardFooter>
              </Card>
            </Col>
            <Col md='8'>
              <Card className='card-chart'>
                <CardHeader>
                  <CardTitle tag='h5'>Tasa de pedidos</CardTitle>
                  <p className='card-category'>
                    Pedidos diarios de la última semana
                  </p>
                </CardHeader>
                <CardBody>
                  <Line
                    data={dashboardNASDAQChart.data}
                    options={dashboardNASDAQChart.options}
                    width={400}
                    height={100}
                  />
                </CardBody>
                <CardFooter>
                  <div className='chart-legend'>
                    <i className='fa fa-circle text-success' /> Correctos
                    {"    "}
                    <i className='fa fa-circle text-danger' /> Fallidos
                    {"      "}
                    <i className='fa fa-circle text-primary' /> En proceso
                    {"   "}
                  </div>
                  <hr />
                  <div className='card-stats'>
                    <i className='fa fa-check' /> Data information certified
                  </div>
                </CardFooter>
              </Card>
            </Col>
          </Row>
          <Row>
            <Col md='12'>
              <Card>
                <CardHeader>
                  <CardTitle tag='h5'>Comportamiento de los clientes</CardTitle>
                  <p className='card-category'>24 horas atrás</p>
                </CardHeader>
                <CardBody>
                  <Line
                    data={dashboard24HoursPerformanceChart.data}
                    options={dashboard24HoursPerformanceChart.options}
                    width={400}
                    height={100}
                  />
                </CardBody>
                <CardFooter>
                  <hr />
                  <div className='stats'>
                    <i className='fa fa-history' /> Actualizar ahora
                  </div>
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </div>
      </>
    );
  }
}

export default Dashboard;
